import { generateRandomArray } from '../../../../../../utils';
const TopProducts = () => {
    return generateRandomArray(5).map((number, index) => (<div key={index} className="d-flex align-items-center px-3 py-2 btn-active-light cursor-pointer">
        <div className="d-flex align-items-center me-0 me-sm-2 mb-3 mb-sm-0 flex-grow-1 justify-content-between w-100 cursor-pointer">
            <div className="sk_img symbol symbol-50 rounded-1 flex-shrink-0 me-2 overflow-hidden"></div>
            <div className="overflow-hidden w-100 pe-2">
                <p className="sk_line sk_line_100 sk_line_height_10 mb-2" style={{ maxWidth: 130 }}></p>
                <span className="sk_line sk_line_50 sk_line_height_8 mb-0 d-block" style={{ maxWidth: 100 }}></span>
            </div>
        </div>
        <span className="sk_line sk_line_50 sk_line_height_12 mb-0 d-block" style={{ maxWidth: 40 }}></span>
    </div>
    ))

}

export default TopProducts;
