import { Component } from "react"
import { Link } from "react-router-dom"
import { OverlayTrigger, Tooltip, Card } from "react-bootstrap"
import { kFormatter } from "../../../../../utils"
import { ImagePlaceholder, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import SkTopProducts from "./SkeletonComponents/SkTopProducts";
import { connect } from "react-redux"
import withRouter from "../../../../routes/WithRouter";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import { checkPermission } from "../../../../../actions/collaboratorActions";
class TopProducts extends Component {

    onErrorImageUpdate = event => {
        event.target.src = ImagePlaceholder
    }
    render() {
        const { topProduct, loader, wallId } = this.props
        return (
            <div className="dashboard_TopPosts dashboard_TopProducts">
                <Card className="card-stretch gutter-b">
                    <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                        <div className="h5 mb-0">
                            Top Performing Products
                            <span className="ms-2">
                                <OverlayTrigger placement='top' overlay={<Tooltip>Now that you know your best sellers, create product  bundles to improve AOV!.</Tooltip>} >
                                    <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                                </OverlayTrigger>
                            </span>
                        </div>
                        <span className="fs-8 fw-semibold">
                            {topProduct?.length > 0 ? 'Assisted Revenue' : ''}
                        </span>
                    </div>
                    <Card.Body>
                        <div className="pe-2" style={{ height: 300, overflowX: 'auto' }}>
                            {loader ?
                                <SkTopProducts />
                                :
                                topProduct && topProduct.length ? topProduct.map((Item, Index) => <Link to={`/analytics#conversion`} onClick={() => localStorage.setItem('conversion_tab', 1)} key={Index} className="d-flex align-items-center px-3 py-2 btn-active-light cursor-pointer text-body active-primary">
                                    <div className="d-flex align-items-center me-0 me-sm-2 mb-3 mb-sm-0 flex-grow-1 justify-content-between w-100 cursor-pointer">
                                        <div className='d-flex align-items-center flex-grow-1'>
                                            <div className="symbol symbol-50 d-flex align-items-center justify-content-center flex-shrink-0 me-2">
                                                <img className="object-center-cover" src={Item.product_image} height={44} width={44} alt={""} onError={this.onErrorImageUpdate} />
                                            </div>
                                            <div className="pe-2 w-100 overflow-hidden">
                                                <p className=" mb-0"><strong className="text-break text-ellipsis d-block">{Item.product_title}</strong></p>
                                                <small className="mb-0 text-gray-600 fw-semibold text-break">{Item.product_sku}</small>
                                            </div>
                                        </div>
                                    </div>
                                    <span className=' fw-bold'>
                                        {Item.discount_currency_symbol}{kFormatter(Item.assisted)}
                                    </span>
                                </Link>
                                ) : <NoCardData title="Manage Product Catalog" desc="Start tagging products to know which of your products are shoppers' top-picks!." methodTitle="Tag Products" method={() => this.props.navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') :''}`)} subTitle="Sync Product" subMethod={() => checkPermission('product_catalog') ? this.props.navigate(`/content/products`) :{}} />

                            }
                        </div>
                    </Card.Body>
                </Card>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        topProduct: state.dashboardData.top_performing_product?.list,
        loader: state.dashboardData.top_performing_product_loader
    }
}

export default connect(mapStateToProps)(withRouter(TopProducts))
